// Custom styling for AliceCarousel
.alice-carousel {
  
  &__stage {
    display: flex;
    align-items: flex-start; /* Change to flex-start to allow for staggered heights */
  }
    
    &:nth-child(odd) {
      margin-top: 1rem;
    }
    
    &:nth-child(even) {
      margin-top: -1rem;
    }
}

.img-container {
	text-align: center;
	
}
